* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

$pink: #ff70d9;
$yellow: #ebff00;
$light: #304571;
$dark: #0d1e48;
$black: #000000;
$white: #ffffff;

body {
	font-family: Lato, sans-serif;
	font-weight: 700;
	color: $white;
	background: linear-gradient(#0d1e48, #304571);
}

/* HEADER */
.header {
	background: $black;
	height: 80px;
	display: flex;

	.header__container {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;
		padding: 24px 20px 20px 20px;
		margin: 0 60px;

		.header__logo {
			width: 104px;
			height: 35px;
		}

		.header__menu {
			/* BURGER MENU */
			.burger-menu-container {
				display: none;
				height: 100%;
				width: 30px;
				cursor: pointer;
				flex-direction: column;
				justify-content: center;
				align-items: center;
			}

			#burger-menu {
				display: none;
			}

			.menu-button,
			.menu-button::before,
			.menu-button::after {
				display: block;
				background-color: $white;
				position: absolute;
				height: 4px;
				width: 30px;
				transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
				border-radius: 2px;
			}

			.menu-button::before {
				content: '';
				margin-top: -8px;
			}

			.menu-button::after {
				content: '';
				margin-top: 8px;
			}

			#burger-menu:checked + .burger-menu-container {
				.menu-button::before {
					margin-top: 0px;
					transform: rotate(405deg);
				}
			}

			#burger-menu:checked + .burger-menu-container {
				.menu-button {
					background: $black;
				}
			}

			#burger-menu:checked + .burger-menu-container {
				.menu-button::after {
					margin-top: 0px;
					transform: rotate(-405deg);
				}
			}
			//

			.header__list {
				display: flex;
				gap: 50px;

				.header__item {
					list-style: none;
					font-size: 18px;
					line-height: 21.6px;

					.item-cart {
						fill: $white;

						&:hover {
						}
					}
				}
			}
		}
	}
}

/* BANNER SECTION*/
.banner {
	height: 490px;
	background-image: linear-gradient(to right, rgba(15, 20, 32, 0.79), rgb(0, 0, 0, 0)), url('./assets/earth.png'),
		url('./assets/background1.jpg');
	background-position: left top, right 60px center, left bottom;
	background-repeat: repeat, no-repeat, repeat;
	background-size: cover, 327px, cover;
	padding: 20px;
	display: flex;

	.banner__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		gap: 22px;
		margin: 60px;
		max-width: 70%;

		.banner__heading {
			font-size: 72px;
			line-height: 72px;
			font-weight: 800;
		}

		.banner__paragraph {
			font-size: 32px;
			line-height: 38px;
			font-weight: 800;
		}

		.banner__button {
			align-self: flex-start;
			color: $black;
			padding: 14px 40px;
			border-radius: 100px;
			background-color: $yellow;
			border: none;
			font-size: 22px;
			line-height: 26px;
			white-space: nowrap;

			&:hover {
				cursor: pointer;
				opacity: 0.6;
			}
		}

		.banner__heading_text-pink {
			color: $pink;
		}

		.banner__heading_text-yellow {
			color: $yellow;
		}
	}
}

/* OFFER CONTENT SECTION*/
.offer-content {
	display: flex;
	flex-direction: column;
	margin: 58px 80px 62px 80px;

	.offer-content__heading {
		font-size: 32px;
		line-height: 38px;
		font-weight: 800;
		margin-bottom: 42px;
	}

	.cards-container {
		display: grid;
		gap: 40px;
		grid-template-areas:
			'card-one card-one'
			'card-two card-three'
			'card-four card-four';

		.card {
			display: flex;
			flex-direction: column;
			justify-content: center;
			gap: 22px;
			height: 350px;
			border-radius: 10px;
			padding: 0 35px;

			.card__heading {
				font-weight: 800;
				max-width: 70%;
			}

			.card__paragraph {
				font-weight: 800;
			}

			.card__button {
				align-self: flex-start;
				color: $yellow;
				padding: 14px 40px;
				border-radius: 100px;
				background-color: transparent;
				border: 2px solid $yellow;
				font-size: 22px;
				line-height: 26px;

				&:hover {
					cursor: pointer;
					opacity: 0.6;
				}
			}
		}

		.card-one {
			grid-area: card-one;
			background-image: linear-gradient(to right, rgba(15, 20, 32, 0.79), rgb(0, 0, 0, 0)),
				url('./assets/card1.jpg');
			background-position: center;
			background-size: cover;

			.card__heading {
				// had to make font sizing dynamic, since between 768 and 1248 screens text goes beyond card
				font-size: 6vw; // absolute font size taken from figma is determined at media queries at the bottom
				line-height: 6vw;
			}

			.card__paragraph {
				font-size: 26px;
				line-height: 31px;
			}
		}

		.card-two {
			grid-area: card-two;
			background-image: linear-gradient(to right, rgba(15, 20, 32, 0.79), rgb(0, 0, 0, 0)),
				url('./assets/card2.jpg');
			background-position: 50% 55%;
			background-size: cover;

			.card__heading {
				font-size: 3.75vw;
				line-height: 3.75vw;
			}

			.card__paragraph {
				font-size: 24px;
				line-height: 28px;
			}
		}

		.card-three {
			grid-area: card-three;
			background-image: linear-gradient(to right, rgba(15, 20, 32, 0.79), rgb(0, 0, 0, 0)),
				url('./assets/card3.jpg');
			background-position: center;
			background-size: cover;

			.card__heading {
				font-size: 3.75vw;
				line-height: 3.75vw;
			}

			.card__paragraph {
				font-size: 24px;
				line-height: 28px;
			}
		}
		.card-four {
			grid-area: card-four;
			background-image: linear-gradient(to right, rgba(15, 20, 32, 0.79), rgb(0, 0, 0, 0)),
				url('./assets/card4.jpg');
			background-position: 50% 45%;
			background-size: cover;

			.card__heading {
				font-size: 6vw;
				line-height: 6vw;
			}

			.card__paragraph {
				font-size: 26px;
				line-height: 31px;
			}
		}
	}
}

/* INFO SECTION */
.info {
	position: relative;
	margin: 60px 80px 43px 80px;
	display: flex;
	flex-direction: column;
	align-items: start;
	gap: 17px;

	.info__heading {
		font-size: 32px;
		line-height: 38px;
		font-weight: 800;
	}

	.info__paragraph {
		font-size: 20px;
		line-height: 24px;
		font-weight: 700;
		opacity: 60%;
	}

	.rest__container {
		position: relative;

		.info__button {
			display: block;
			font-size: 20px;
			line-height: 24px;
			margin-bottom: 17px;
			font-weight: 800;
			color: $yellow;
			background: transparent;
			border: none;

			&:hover {
				cursor: pointer;
				opacity: 60%;
			}
		}

		#info_rest {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;

			&:checked ~ .info__paragraph_collapsed {
				max-height: 400px;
			}

			&:hover {
				cursor: pointer;
			}
		}

		.info__paragraph_collapsed {
			font-size: 20px;
			line-height: 24px;
			font-weight: 700;
			opacity: 60%;
			max-height: 0;
			overflow: hidden;
			transition: max-height 400ms ease;
		}
	}
}

/* FOOTER */
.footer {
	background: $black;
	height: 127px;
	padding: 0 80px;

	.footer__container {
		display: flex;
		flex-direction: column;
		align-items: center;

		.footer__heading {
			font-size: 18px;
			line-height: 28px;
			font-weight: 800;
			color: $yellow;
			white-space: nowrap;
		}
	}
}

/* MEDIA QUERIES */

@media (min-width: 1248px) {
	.offer-content {
		.cards-container {
			.card-one,
			.card-four {
				.card__heading {
					font-size: 74px;
					line-height: 74px;
				}
			}
			.card-two,
			.card-three {
				.card__heading {
					font-size: 46px;
					line-height: 46px;
				}
			}
		}
	}
}

@media (max-width: 768px) {
	.header {
		.header__container {
			margin: 0 10px;

			.header__logo {
				width: 71px;
				height: 24px;
			}
		}
	}

	.banner {
		height: 420px;
		background-size: cover, 300px, cover;
		background-position: left top, right -90px center, left bottom;

		.banner__content {
			margin: 60px 10px;

			.banner__heading {
				font-size: 48px;
				line-height: 48px;
			}

			.banner__paragraph {
				font-size: 22px;
				line-height: 26px;
			}
		}
	}

	.offer-content {
		margin: 55px 30px;

		.offer-content__heading {
			font-size: 24px;
			line-height: 28px;
			margin-bottom: 30px;
		}

		.cards-container {
			gap: 30px;
			grid-template-areas:
				'card-one card-two'
				'card-three card-four';

			.card {
				justify-content: flex-end;
				padding: 25px;

				.card__heading {
					max-width: 100%;
				}

				.card__paragraph {
					font-size: 16px;
					line-height: 19px;
				}

				.card__button {
					padding: 7px 40px;
					font-size: 16px;
					line-height: 19px;
				}
			}
			.card-one {
				.card__heading {
					font-size: 40px;
					line-height: 40px;
				}
			}

			.card-two {
				.card__heading {
					font-size: 40px;
					line-height: 40px;
				}
			}

			.card-three {
				.card__heading {
					font-size: 40px;
					line-height: 40px;
				}
			}
			.card-four {
				.card__heading {
					font-size: 40px;
					line-height: 40px;
				}
			}
		}
	}

	.info {
		margin: 53px 30px 40px 30px;
		gap: 13px;

		.info__heading {
			font-size: 24px;
			line-height: 29px;
		}

		.info__paragraph {
			font-size: 16px;
			line-height: 19px;
		}

		.rest__container {
			.info__paragraph_collapsed {
				font-size: 16px;
				line-height: 19px;
			}

			.info__button {
				font-size: 16px;
				line-height: 19px;
				margin-bottom: 13px;
			}
		}
	}
}

@media (min-width: 360px) and (max-width: 576px) {
	.header {
		.header__container {
			.header__menu {
				.header__list {
					gap: 20px;
				}
			}
		}
	}

	.offer-content {
		.cards-container {
			gap: 3vw;

			.card {
				padding: 4vw;
			}

			.card-one,
			.card-two,
			.card-three,
			.card-four {
				.card__heading {
					font-size: 6vw;
					line-height: 6vw;
				}
			}
		}
	}
}

@media (min-width: 360px) {
	.header {
		.header__container {
			.header__menu {
				.header__list {
					.header__item {
						&:hover {
							cursor: pointer;
							background-color: $black;
							background-image: linear-gradient(to right, $yellow, $pink);
							-webkit-background-clip: text;
							-moz-background-clip: text;
							-webkit-text-fill-color: transparent;
							-moz-text-fill-color: transparent;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 360px) {
	.header {
		height: 48px;

		.header__container {
			margin: 0;
			padding: 12px 21px;

			.header__menu {
				/* BURGER MENU */
				.burger-menu-container {
					display: flex;
				}
				.header__list {
					gap: 0;
					position: absolute;
					top: 0;
					margin-top: 48px;
					left: 0;
					flex-direction: column;
					width: 100%;
					justify-content: center;
					align-items: center;
				}
				#burger-menu ~ .header__list {
					.header__item {
						overflow: hidden;
						height: 0;
						margin: 0;
						padding: 0;
						border: 0;
						transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
					}
				}
				#burger-menu:checked ~ .header__list {
					.header__item {
						display: flex;
						align-items: center;
						border: 1px solid $black;
						height: 48px;
						transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
					}
				}
				.header__list > .header__item {
					display: flex;
					justify-content: center;
					width: 100%;
					color: $white;
					background-color: $dark;
					&:hover {
						background-color: $light;
						cursor: pointer;
					}
				}
				.header__list > .header__item:not(:last-child) {
					border-bottom: 1px solid $dark;
				}
				//
			}
		}
	}

	.banner {
		height: 451px;
		background-image: linear-gradient(to top, rgba(15, 20, 32, 0.79), rgb(0, 0, 0, 0)), url('./assets/earth.png'),
			url('./assets/background1.jpg');
		background-position: top, top -110px center, top;

		.banner__content {
			display: flex;
			justify-content: flex-end;
			text-align: center;
			gap: 17px;
			margin: 25px 0;
			max-width: 100%;

			.banner__heading {
				font-size: 36px;
				line-height: 36px;
			}

			.banner__paragraph {
				font-size: 16px;
				line-height: 19px;
				white-space: normal;
			}

			.banner__button {
				padding: 7px 40px;
				font-size: 16px;
				line-height: 19px;
				align-self: center;
				white-space: nowrap;
			}
		}
	}

	.offer-content {
		margin: 50px 20px;

		.offer-content__heading {
			font-size: 18px;
			line-height: 21px;
		}

		.cards-container {
			grid-template-areas:
				'card-one'
				'card-two'
				'card-three'
				'card-four';

			.card {
				justify-content: flex-end;
				gap: 22px;
				padding: 25px;

				.card__heading {
					font-size: 40px;
					line-height: 40px;
				}
			}
		}
	}

	.info {
		margin: 50px 20px 30px 20px;
		gap: 20px;

		.info__heading {
			font-size: 18px;
			line-height: 21px;
		}

		.info__paragraph {
			font-size: 14px;
			line-height: 17px;
		}

		.rest__container {
			.info__paragraph_collapsed {
				font-size: 14px;
				line-height: 17px;
			}

			.info__button {
				font-size: 14px;
				line-height: 17px;
				margin-bottom: 20px;
			}
		}
	}
}

@media (max-width: 300px) {
	// making font size dynamic to avoid text going beyond cards
	.offer-content {
		.cards-container {
			.card-one,
			.card-two,
			.card-three,
			.card-four {
				.card__heading {
					font-size: 12vw;
					line-height: 12vw;
				}
			}
		}
	}
}
